@import url('https://fonts.cdnfonts.com/css/product-sans');

.imagecard-wrapper{
    position: relative;
    width: 360px;
    height: 330px;
    margin: 10px;
    cursor: pointer;
    box-shadow: 0 0.3vw 0.6vw 0 #00000066;
    border-radius: 10px 10px 10px 10px;
    background-color:#c1440e;
}

.imagecard-img{
    border-radius: 10px 10px 10px 10px;
    width: 100%;
    height: 100%;  
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    justify-content: center;
}

.imagecard-info{
    display: flex;
    flex-direction: column;
    text-align: center;
    font-family: 'Product Sans', sans-serif;
    color:#fff;
    font-size: 1.1rem;
    font-weight: 300;
    letter-spacing: 1.5px;
}

.imagecard-imgText {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 8px 15px;
    background: rgb(255, 255, 255);
    color: tomato;
    margin-bottom: 10px;
    font-family: 'Product Sans', sans-serif;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 1px;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: visibility 0s, opacity 0.5s linear; 
    transition: visibility 0s, opacity 0.5s linear;
    transition: visibility 0.5s, opacity 0.5s ease-out;
    -webkit-transition: visibility 0.5s, opacity 0.5s ease-out;
}
.imagecard-wrapper:hover div {
    cursor: pointer;
    visibility: visible;
    opacity: 0.9; 
}
@media screen and (max-width: 1024px){
    .imagecard-wrapper{
        width: 230px;
        height: 190px;
        margin: 10px;
        cursor: pointer;
        box-shadow: 0 0.3vw 0.6vw 0 #00000066;
        border-radius: 10px 10px 10px 10px;
        background-color:#c1440e;
    }
}

@media screen and (max-width: 480px){
    .imagecard-wrapper{
        width: 140px;
        height: 120px;
        margin: 10px;
        cursor: pointer;
        box-shadow: 0 0.3vw 0.6vw 0 #00000066;
        border-radius: 10px 10px 10px 10px;
        background-color:#c1440e;
    }
}

