.imageViewer-viwer{
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow-y: hidden;
    background-color: rgb(255, 255, 255); /* Fallback color */
    background-color: rgba(255, 255, 255, 0.4); /* Black w/ opacity */
    /* Centers all components */
    display: flex;
    align-items: center;
    justify-content: center;  
}

.imageViewer-container{
    position: relative;
    border-radius: 10px;
    height: 90vh;
    width: 65vw;
    margin: 5%;
    background-color: white;
}

.imageViewer-img{
    display:flex;
    flex-wrap: wrap;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    object-fit: fill;
    margin: 0 auto;    
    border: 3px solid black;
}

.imageViewer-header {
    position:absolute;
    top: 2px;
    right: 10px;
    font-size: 28px;
    color: white;
    background-color: transparent;
    border: none;
    z-index: 1;
    filter: drop-shadow(0px 0.2vw 0.5vw #000);
}
.imageViewer-header-container{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    vertical-align: text-top;
}

.imageViewer-download{
    background-color: transparent;
    color:white;
    border: none;
    margin: 10px;
    font-size: 2.5rem;
    cursor: pointer;
}

.imageViewer-close{
    background-color: transparent;
    color:white;
    border: none;
    margin: 10px;
    font-size: 3rem;
    cursor: pointer;
}

.viewer-open {
    display: flex;
    opacity: 1;
    animation: modal-anim 0.25s forwards;
}

.viewer-closed {
    display: none;
}

.fade-in {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 150ms;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@media screen and (max-width: 1024px){
    .imageViewer-container{
        height: 60vh;
        width: 90vw;
        margin: 5%;
    }
    .imageViewer-download{
        font-size: 2.5rem;
    }
    
    .imageViewer-close{
        margin: 10px;
        font-size: 2.8rem;        
    }
    
}

@media screen and (max-width: 480px){
    .imageViewer-container{
        height: 50vh;
        width: 90vw;
    }
    .imageViewer-download{
        margin: 5px;
        font-size: 2rem;
    }
    
    .imageViewer-close{
        margin: 5px;
        font-size: 2.3rem;        
    }
    
}
